.paymentContainer {
  background: linear-gradient(to top, #0a0a0a, #1a1a1d, #222226);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #e5e5e5;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 2rem;
}

.paymentBox {
  background: #2a2a2f;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 0 25px rgba(114, 210, 236, 0.3);
  width: 100%;
  max-width: 400px;
}

.paymentBox h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #00ffcc, #00aaff, #0044ff);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  animation: gradientMove 4s infinite alternate;
}

@keyframes gradientMove {
  from { background-position: 0% 50%; }
  to { background-position: 100% 50%; }
}

.paymentBox p {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.inputField {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 10px;
  border: none;
  outline: none;
  font-size: 1rem;
  background: #1f1f23;
  color: #e5e5e5;
}

.row {
  display: flex;
  gap: 10px;
  justify-content: space-between;
}

.halfInput {
  width: 48%;
}

.payBtn {
  background: linear-gradient(90deg,  #00aaff, #0044ff);
  border: none;
  padding: 12px 24px;
  border-radius: 12px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  font-size: 1rem;
  margin-top: 10px;
  margin-left: 20px;
}

.payBtn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(26, 191, 213, 0.3);
}

.payBtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.successMsg {
  color: #4ade80; /* ירוק בהיר */
  margin-top: 1.2rem;
  font-size: 1rem;
}
